import React from 'react'
import ReactDOM from 'react-dom'
import { HashRouter, Routes, Route, Navigate, useNavigate, useParams, useLocation } from 'react-router-dom'
import { useCallback, useEffect, useState, useRef } from "react"
import './App.css'
import BigNumber from 'bignumber.js'
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

// 将 formatTime 移到全局作用域
const formatTime = (start, end) => {
    if (!start || !end || end < start) {
        return '0分0秒';
    }
    const seconds = Math.floor((end - start) / 1000);
    const minutes = Math.floor(seconds / 60);
    const remainingSeconds = seconds % 60;
    return `${minutes}分${remainingSeconds}秒`;
}

// 将 menuItems 移到全局作用域，因为它在多个组件中使用
const menuItems = [
    { key: 'multiply', label: '乘法', icon: '×' },
    { key: 'add', label: '加法', icon: '+' },
    { key: 'subtract', label: '减法', icon: '-' },
    { key: 'percentage', label: '百化分', icon: '%' },
];

// 主页面 - 选择练习类型
function Home() {
    const navigate = useNavigate();

    const handleTypeSelect = (selectedType) => {
        navigate(`/practice/${selectedType}`);
    };

    return (
        <div className='wrapper'>
            <div style={{
                boxSizing: 'border-box',
                padding: '20px 15px',
                background: '#f5f5f5',
                borderRadius: '6px',
                margin: '15px',
                boxShadow: '0 2px 8px rgba(0,0,0,0.05)'
            }}>
                <h2 style={{
                    margin: '0 0 15px 0',
                    fontSize: '18px',
                    color: '#333',
                    textAlign: 'center'
                }}>
                    选择练习类型
                </h2>
                <div style={{
                    display: 'grid',
                    gridTemplateColumns: 'repeat(2, 1fr)',
                    gap: '12px',
                    padding: '2px'
                }}>
                    {menuItems.map(item => (
                        <div
                            key={item.key}
                            onClick={() => handleTypeSelect(item.key)}
                            style={{
                                padding: '15px',
                                borderRadius: '6px',
                                cursor: 'pointer',
                                backgroundColor: 'white',
                                color: '#333',
                                display: 'flex',
                                flexDirection: 'column',
                                alignItems: 'center',
                                gap: '8px',
                                boxShadow: '0 1px 2px rgba(0,0,0,0.1)',
                                transition: 'all 0.3s ease',
                                fontSize: '16px'
                            }}
                        >
                            <div style={{ fontSize: '24px', fontWeight: 'bold' }}>
                                {item.icon}
                            </div>
                            <div>{item.label}</div>
                        </div>
                    ))}
                </div>
            </div>
        </div>
    )
}

// 答题页面
function Practice() {
    const { type } = useParams();
    const navigate = useNavigate();
    const [list, setList] = useState([])
    const [startTime, setStartTime] = useState(null)
    const [endTime, setEndTime] = useState(null)
    const [currentTime, setCurrentTime] = useState(null)
    const refList = useRef([])
    const timerRef = useRef(null)
    const [isLoading, setIsLoading] = useState(false)
    const [showResultModal, setShowResultModal] = useState(false)
    const [currentIndex, setCurrentIndex] = useState(0);
    const [completedList, setCompletedList] = useState([]);
    const [toastMessage, setToastMessage] = useState('');
    const [showToast, setShowToast] = useState(false);

    const getNumber = (min, max) => {
        let randomNumber = 0
        while (1) {
            randomNumber = Math.ceil(Math.random() * max)
            if (randomNumber > min && randomNumber % 10 !== 0) {
                break;
            }
        }
        return randomNumber;
    }

    const generateList = useCallback(() => {
        if (!type) return [];

        let list = []
        const questionCount = type === 'percentage' ? 18 : 30; // 百分数18题，其他类型30题

        if (type === 'percentage') {
            // 生成1/2到1/19的分数题目
            for (let i = 2; i <= 19; i++) {
                list.push({
                    question: { a: 1, b: i },
                    operator: '/',
                    answer: parseFloat((new BigNumber(1 / i)).multipliedBy(100).toFixed(1)),
                    id: Math.random()
                });
                console.log(parseFloat((new BigNumber(1 / i)).multipliedBy(100).toFixed(1)))
            }
        } else {
            // 其他类型题目保持原样
            for (let i = 0; i < questionCount; i = i + 1) {
                let a;
                let b;
                let operator;
                let answer;
                switch (type) {
                    case 'multiply':
                        a = getNumber(9, 99);
                        b = getNumber(2, 9);
                        operator = '*';
                        answer = a * b;
                        break;
                    case 'add':
                        a = getNumber(100, 999);
                        b = getNumber(10, 99);
                        operator = '+';
                        answer = a + b;
                        break;
                    case 'subtract':
                        a = getNumber(100, 999);
                        b = getNumber(10, 99);
                        operator = '-';
                        answer = a - b;
                        break;
                }

                list.push({
                    question: { a, b },
                    operator,
                    id: Math.random(),
                    answer,
                })
            }
        }

        return list;
    }, [type])

    useEffect(() => {
        let newList = generateList()
        setList(newList)
    }, [generateList])

    const scrollToInput = (inputElement) => {
        if (!inputElement) return;

        const rect = inputElement.getBoundingClientRect();
        const bottomBarHeight = 200; // 底部按钮的高度
        const headerHeight = 80; // 给顶部预留一些空间
        const isInViewport = (rect.bottom <= (window.innerHeight - bottomBarHeight)
        );
        console.log(rect.bottom, 'rect.bottom===')
        if (!isInViewport) {
            // 滚动到顶部而不是中间
            inputElement.scrollIntoView({ block: 'start', behavior: 'smooth' });
        }
    }


    const onChange = (v, index) => {
        const newList = [...list];
        const currentItem = newList[index];
        const isCorrect = v === currentItem.answer

        newList[index] = {
            ...currentItem,
            userAnswer: v
        };

        // 显示 toast 提示
        if (isCorrect) {
            toast.success('答对了！', {
                hideProgressBar: true,
                closeOnClick: true,
                pauseOnHover: false,
                draggable: false,
                progress: undefined,
                theme: "light",
            });
        } else {
            toast.error(`答错了，正确答案是 ${currentItem.answer}`, {
                hideProgressBar: true,
                closeOnClick: true,
                pauseOnHover: false,
                draggable: false,
                progress: undefined,
                theme: "light",
            });
        }

        // 如果是最后一题，先更新答案再提交
        if (index === list.length - 1) {
            setList(newList);
            setTimeout(() => {
                const now = new Date();
                setEndTime(now);
                setCompletedList(newList);
                onComplete({
                    completedList: newList,
                    startTime,
                    endTime: now
                });
            }, isCorrect ? 1000 : 1500); // 根据是否正确调整延迟时间
        } else {
            setList(newList);
            setCurrentIndex(index + 1);
        }
    }


    const getNewList = () => {
        const newList = generateList();
        ReactDOM.flushSync(() => {
            setList(newList);
            setCurrentIndex(0); // 重置当前题目索引
        });
        refList.current[0].focus();
        const now = new Date();
        setStartTime(now);
        setCurrentTime(now);
        setEndTime(null);
    }

    const getTimeSpent = () => {
        if (!startTime || !endTime) return null;
        return formatTime(startTime, endTime);
    }

    useEffect(() => {
        if (startTime && !endTime) {
            timerRef.current = setInterval(() => {
                setCurrentTime(new Date());
            }, 1000);
        } else {
            if (timerRef.current) {
                clearInterval(timerRef.current);
            }
        }
        return () => {
            if (timerRef.current) {
                clearInterval(timerRef.current);
            }
        };
    }, [startTime, endTime]);

    const handleTypeSelect = (selectedType) => {
        navigate(`/practice/${selectedType}`, { replace: true });
        setCurrentIndex(0);
        setCompletedList([]);
        const now = new Date();
        setStartTime(now);
        setCurrentTime(now);
        setEndTime(null);
        const newList = generateList();
        ReactDOM.flushSync(() => {
            setList(newList);
        });
        setTimeout(() => {
            if (refList.current[0]) {
                refList.current[0].focus();
            }
        }, 0);
    };

    const getStats = () => {
        const total = completedList.length;
        const correct = completedList.filter(item => item.userAnswer === item.answer).length;
        const accuracy = Math.round((correct / total) * 100);
        return { total, correct, accuracy };
    }

    const showToastMessage = (message) => {
        setToastMessage(message);
        setShowToast(true);
        setTimeout(() => {
            setShowToast(false);
        }, 2000);
    };

    const onComplete = (results) => {
        navigate('/result', {
            state: { results, type },
            replace: true
        });
    };

    // 初始化时设置开始时间
    useEffect(() => {
        const now = new Date();
        setStartTime(now);
        setCurrentTime(now);
        setEndTime(null);
    }, []); // 空依赖数组，只在组件挂载时执行一次

    return (
        <div className='wrapper'>
            {/* 菜单部分始终显示 */}
            <div style={{
                boxSizing: 'border-box',
                padding: (!type && !endTime) ? '20px 15px' : '8px 4px',
                background: '#f5f5f5',
                borderRadius: '6px',
                margin: type || endTime ? '0' : '15px',
                boxShadow: '0 2px 8px rgba(0,0,0,0.05)',
                transition: 'all 0.3s ease',
                position: type || endTime ? 'sticky' : 'relative',
                top: type || endTime ? '0' : 'auto',
                zIndex: 1000,
                width: type || endTime ? '100%' : 'auto',
                overflow: 'hidden'
            }}>
                {!type && !endTime && (
                    <h2 style={{
                        margin: '0 0 15px 0',
                        fontSize: '18px',
                        color: '#333',
                        textAlign: 'center'
                    }}>
                        选择练习类型
                    </h2>
                )}
                <div style={{
                    display: 'grid',
                    gridTemplateColumns: (!type && !endTime) ? 'repeat(2, 1fr)' : 'repeat(4, 1fr)',
                    gap: (!type && !endTime) ? '12px' : '4px',
                    padding: '2px'
                }}>
                    {menuItems.map(item => (
                        <div
                            key={item.key}
                            onClick={() => handleTypeSelect(item.key)}
                            style={{
                                padding: (!type && !endTime) ? '15px' : '6px 2px',
                                height: (!type && !endTime) ? 'auto' : '40px',
                                borderRadius: '6px',
                                cursor: 'pointer',
                                backgroundColor: type === item.key ? '#4A90E2' : 'white',
                                color: type === item.key ? 'white' : '#333',
                                display: 'flex',
                                flexDirection: (!type && !endTime) ? 'column' : 'row',
                                alignItems: 'center',
                                justifyContent: 'center',
                                gap: (!type && !endTime) ? '8px' : '3px',
                                boxShadow: '0 1px 2px rgba(0,0,0,0.1)',
                                transition: 'all 0.3s ease',
                                fontSize: (!type && !endTime) ? '16px' : '12px',
                                minWidth: type || endTime ? '50px' : 'auto',
                                userSelect: 'none',
                                whiteSpace: 'nowrap'
                            }}
                        >
                            <div style={{
                                fontSize: (!type && !endTime) ? '24px' : '15px',
                                fontWeight: 'bold',
                                minWidth: type || endTime ? '14px' : 'auto',
                                lineHeight: '1'
                            }}>
                                {item.icon}
                            </div>
                            <div style={{
                                whiteSpace: 'nowrap',
                                fontSize: type || endTime ? '12px' : '16px',
                                padding: '0 1px'
                            }}>
                                {item.label}
                            </div>
                        </div>
                    ))}
                </div>
            </div>

            {/* 结果显示部分 */}
            {!type && endTime && (
                <div style={{
                    padding: '15px 10px',
                    backgroundColor: 'white'
                }}>
                    <h3 style={{
                        textAlign: 'center',
                        marginBottom: '20px',
                        color: '#333',
                        fontSize: '20px'
                    }}>
                        本次练习结果
                    </h3>
                    <div style={{
                        marginBottom: '20px',
                        fontSize: '16px',
                        color: '#666',
                        lineHeight: '1.8',
                        padding: '0 5px'
                    }}>
                        <p>完成用时：{getTimeSpent()}</p>
                        {(() => {
                            const { total, correct, accuracy } = getStats();
                            return (
                                <>
                                    <p>题目总数：{total}</p>
                                    <p>正确题数：{correct}</p>
                                    <p>正确率：{accuracy}%</p>
                                </>
                            );
                        })()}
                    </div>

                    {/* 修改题目详情列表样式 */}
                    <div style={{
                        marginTop: '20px',
                        borderTop: '1px solid #eee',
                        paddingTop: '20px'
                    }}>
                        <h4 style={{
                            fontSize: '16px',
                            color: '#333',
                            marginBottom: '15px',
                            textAlign: 'center'
                        }}>
                            答题详情
                        </h4>
                        <div style={{
                            display: 'flex',
                            flexDirection: 'column',
                            gap: '8px'
                        }}>
                            {completedList.map((item, index) => (
                                <div
                                    key={item.id}
                                    style={{
                                        display: 'flex',
                                        alignItems: 'center',
                                        justifyContent: 'space-between',
                                        padding: '10px 8px',
                                        backgroundColor: '#f9f9f9',
                                        borderRadius: '6px',
                                        fontSize: '14px'
                                    }}
                                >
                                    <div style={{ display: 'flex', alignItems: 'center', gap: '6px' }}>
                                        <span style={{ color: '#666' }}>{index + 1}.</span>
                                        <span>
                                            {item.question.a} {item.operator} {item.question.b} =
                                        </span>
                                        <span style={{
                                            color: item.userAnswer === item.answer ? '#4CAF50' : '#F44336',
                                            fontWeight: '500'
                                        }}>
                                            {item.userAnswer}
                                        </span>
                                    </div>
                                    <div style={{
                                        display: 'flex',
                                        alignItems: 'center',
                                        gap: '6px'
                                    }}>
                                        {item.userAnswer !== item.answer && (
                                            <span style={{ color: '#666', fontSize: '13px' }}>
                                                正确答案: {item.answer}
                                            </span>
                                        )}
                                        <span style={{
                                            padding: '2px 6px',
                                            borderRadius: '4px',
                                            fontSize: '12px',
                                            backgroundColor: item.userAnswer === item.answer ? '#E8F5E9' : '#FFEBEE',
                                            color: item.userAnswer === item.answer ? '#4CAF50' : '#F44336'
                                        }}>
                                            {item.userAnswer === item.answer ? '正确' : '错误'}
                                        </span>
                                    </div>
                                </div>
                            ))}
                        </div>
                    </div>
                </div>
            )}

            {/* 答题部分 */}
            {type && (
                <>
                    {startTime && !endTime && currentTime && (
                        <div style={{
                            textAlign: 'center',
                            margin: '15px',
                            fontSize: '16px',
                            color: '#666'
                        }}>
                            当前用时：{formatTime(startTime, currentTime)}
                        </div>
                    )}

                    {/* 题目进度 */}
                    <div style={{
                        textAlign: 'center',
                        margin: '15px',
                        fontSize: '16px',
                        color: '#666'
                    }}>
                        {currentIndex + 1} / {list.length}
                    </div>

                    {/* 单个题目展示 */}
                    <div style={{
                        padding: '20px 15px',
                        margin: '10px',
                        backgroundColor: 'white',
                        borderRadius: '6px',
                        boxShadow: '0 1px 3px rgba(0,0,0,0.05)'
                    }}>
                        {list[currentIndex] && (
                            <QuestionItem
                                ref={node => {
                                    refList.current[currentIndex] = node
                                }}
                                key={list[currentIndex].id}
                                operator={list[currentIndex].operator}
                                index={currentIndex}
                                userAnswer={list[currentIndex].userAnswer}
                                answer={list[currentIndex].answer}
                                question={list[currentIndex].question}
                                onChange={v => onChange(v, currentIndex)}
                                refList={refList}
                                showToastMessage={showToastMessage}
                            />
                        )}
                    </div>

                    {/* 为固定底部按钮留出空间 */}
                    <div style={{ height: '80px' }} />
                </>
            )}

            {(type || endTime) && (
                <div style={{ height: '50px' }} />
            )}

            {/* Toast 提示 */}
            {showToast && (
                <div style={{
                    position: 'fixed',
                    bottom: '100px',
                    left: '50%',
                    transform: 'translateX(-50%)',
                    backgroundColor: 'rgba(0, 0, 0, 0.7)',
                    color: 'white',
                    padding: '10px 20px',
                    borderRadius: '20px',
                    fontSize: '14px',
                    zIndex: 2000,
                    animation: 'fadeIn 0.3s ease'
                }}>
                    {toastMessage}
                </div>
            )}
        </div>
    )
}

const QuestionItem = React.forwardRef((props, ref) => {
    const { a, b } = props.question;
    const formRef = useRef();

    const handleSubmit = (e) => {
        e.preventDefault();
        const form = e.target;
        const value = form.elements[0].value;

        if (value) {
            props.onChange(Number(value), props.index);
            form.reset();
        }
    }

    const isAnswerCorrect = (userAnswer, correctAnswer, operator, question) => {
        if (operator === '/') {
            // 对于百化分题目，计算精确值并比较
            const exactAnswer = (1 / question.b) * 100;
            const userNumber = Number(userAnswer);

            // 检查用户答案是否在可接受范围内
            return Math.abs(userNumber - exactAnswer) < 0.01; // 允许0.01的误差
        }
        // 其他类型题目保持原样比较
        return userAnswer === correctAnswer;
    }

    let color = 'black';
    if (props.userAnswer == undefined) color = 'black';
    else if (!isAnswerCorrect(props.userAnswer, props.answer, props.operator, props.question)) color = 'red';
    else color = 'green'

    return (
        <div style={{
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            gap: '16px'
        }}>
            <div style={{
                display: 'flex',
                alignItems: 'center',
                gap: '12px',
                fontSize: '28px',
                fontWeight: '500'
            }}>
                <span><RenderNumber number={props.question.a} /></span>
                <span style={{ color: '#666' }}>{props.operator}</span>
                <span><RenderNumber number={props.question.b} /></span>
                <span style={{ color: '#666' }}>=</span>
            </div>
            <form
                autoComplete='off'
                ref={formRef}
                style={{ width: '100%', maxWidth: '140px' }}
                onSubmit={handleSubmit}
            >
                <input
                    type='number'
                    step='any'
                    ref={ref}
                    name='answer'
                    style={{
                        width: '100%',
                        color: '#333',
                        padding: '12px 0',
                        border: 'none',
                        borderBottom: '2px solid #E0E0E0',
                        borderRadius: '0',
                        fontSize: '32px',
                        fontWeight: '500',
                        textAlign: 'center',
                        outline: 'none',
                        backgroundColor: 'transparent',
                        WebkitAppearance: 'none',
                        MozAppearance: 'textfield',
                        transition: 'all 0.3s ease',
                        caretColor: '#4A90E2'
                    }}
                    autoFocus
                    inputMode="decimal"
                    onInvalid={(e) => {
                        e.preventDefault();
                    }}
                    placeholder="?"
                />
            </form>
        </div>
    )
});

const RenderNumber = (props) => {
    const { number } = props;
    const stringNumber = String(number);

    // 直接返回数字，不添加空格
    return <span>{stringNumber}</span>;
}

// 结果页面
function Result() {
    const location = useLocation();
    const navigate = useNavigate();
    const { results, type } = location.state || {};

    if (!results) {
        return <Navigate to="/" />;
    }

    const { completedList, startTime, endTime } = results;

    const getTimeSpent = () => {
        if (!startTime || !endTime) return null;
        return formatTime(new Date(startTime), new Date(endTime));
    }

    const getStats = () => {
        const total = completedList.length;
        const correct = completedList.filter(item => {
            if (type === 'percentage') {
                // 对于百化分题目，允许0.01的误差
                const exactAnswer = (1 / item.question.b) * 100;
                return Math.abs(item.userAnswer - exactAnswer) < 0.01;
            }
            return item.userAnswer === item.answer;
        }).length;
        const accuracy = Math.round((correct / total) * 100);
        return { total, correct, accuracy };
    }

    return (
        <div style={{
            padding: '15px 10px',
            backgroundColor: 'white'
        }}>
            <h3 style={{
                textAlign: 'center',
                marginBottom: '20px',
                color: '#333',
                fontSize: '20px'
            }}>
                本次练习结果
            </h3>
            <div style={{
                marginBottom: '20px',
                fontSize: '16px',
                color: '#666',
                lineHeight: '1.8',
                padding: '0 5px'
            }}>
                <p>完成用时：{getTimeSpent()}</p>
                {(() => {
                    const { total, correct, accuracy } = getStats();
                    return (
                        <>
                            <p>题目总数：{total}</p>
                            <p>正确题数：{correct}</p>
                            <p>正确率：{accuracy}%</p>
                        </>
                    );
                })()}
            </div>

            {/* 答题详情列表 */}
            <div style={{
                marginTop: '20px',
                borderTop: '1px solid #eee',
                paddingTop: '20px'
            }}>
                <h4 style={{
                    fontSize: '16px',
                    color: '#333',
                    marginBottom: '15px',
                    textAlign: 'center'
                }}>
                    答题详情
                </h4>
                <div style={{
                    display: 'flex',
                    flexDirection: 'column',
                    gap: '8px'
                }}>
                    {completedList.map((item, index) => {
                        const isCorrect = type === 'percentage'
                            ? Math.abs(item.userAnswer - ((1 / item.question.b) * 100)) < 0.01
                            : item.userAnswer === item.answer;

                        return (
                            <div
                                key={item.id}
                                style={{
                                    display: 'flex',
                                    alignItems: 'center',
                                    justifyContent: 'space-between',
                                    padding: '10px 8px',
                                    backgroundColor: '#f9f9f9',
                                    borderRadius: '6px',
                                    fontSize: '14px'
                                }}
                            >
                                <div style={{ display: 'flex', alignItems: 'center', gap: '6px' }}>
                                    <span style={{ color: '#666' }}>{index + 1}.</span>
                                    <span>
                                        {item.question.a} {item.operator} {item.question.b} =
                                    </span>
                                    <span style={{
                                        color: isCorrect ? '#4CAF50' : '#F44336',
                                        fontWeight: '500'
                                    }}>
                                        {item.userAnswer}
                                    </span>
                                </div>
                                <div style={{
                                    display: 'flex',
                                    alignItems: 'center',
                                    gap: '6px'
                                }}>
                                    {!isCorrect && (
                                        <span style={{ color: '#666', fontSize: '13px' }}>
                                            正确答案: {type === 'percentage'
                                                ? ((1 / item.question.b) * 100).toFixed(1)
                                                : item.answer}
                                        </span>
                                    )}
                                    <span style={{
                                        padding: '2px 6px',
                                        borderRadius: '4px',
                                        fontSize: '12px',
                                        backgroundColor: isCorrect ? '#E8F5E9' : '#FFEBEE',
                                        color: isCorrect ? '#4CAF50' : '#F44336'
                                    }}>
                                        {isCorrect ? '正确' : '错误'}
                                    </span>
                                </div>
                            </div>
                        );
                    })}
                </div>
            </div>

            {/* 底部按钮 */}
            <div style={{ height: '80px' }} />
            <div style={{
                position: 'fixed',
                bottom: '0',
                left: '0',
                right: '0',
                padding: '15px',
                display: 'flex',
                gap: '10px',
                justifyContent: 'center',
                backgroundColor: 'white',
                borderTop: '1px solid #eee'
            }}>
                <button onClick={() => navigate('/')} style={{
                    padding: '10px 20px',
                    borderRadius: '6px',
                    border: 'none',
                    backgroundColor: '#f5f5f5',
                    color: '#666'
                }}>
                    返回首页
                </button>
                <button onClick={() => navigate(`/practice/${type}`)} style={{
                    padding: '10px 20px',
                    borderRadius: '6px',
                    border: 'none',
                    backgroundColor: '#4A90E2',
                    color: 'white'
                }}>
                    继续练习
                </button>
            </div>
        </div>
    );
}

function App() {
    return (
        <HashRouter>
            <ToastContainer
                position="top-center"
                autoClose={600}
                hideProgressBar={false}
                newestOnTop={false}
                closeOnClick
                rtl={false}
                pauseOnFocusLoss={false}
                draggable={false}
                pauseOnHover={false}
                theme="light"
            />
            <Routes>
                <Route path="/" element={<Home />} />
                <Route path="/practice/:type" element={<Practice />} />
                <Route path="/result" element={<Result />} />
            </Routes>
        </HashRouter>
    )
}

ReactDOM.render(<App />, document.getElementById('root'))
